@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&family=Press+Start+2P&family=Roboto+Mono:wght@300;400;500&display=swap');

:root {
  --primary: #64ffda;
  --secondary: #8892b0;
  --bg-dark: #0a192f;
  --bg-light: #112240;
  --text: #e6f1ff;
}

.resume-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 2rem 1.5rem;
  color: var(--text);
  font-family: 'Space Grotesk', sans-serif;
  background: var(--bg-dark);
  line-height: 1.7;
  position: relative;
  overflow: hidden;
}

/* Tetris Background Animation */
.tetris-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 0.03;
  pointer-events: none;
}

/* Tetris Block Animation */
@keyframes tetrisDrop {
  0% { transform: translateY(-100px) rotate(0deg); opacity: 0; }
  10% { opacity: 1; }
  90% { opacity: 1; }
  100% { transform: translateY(calc(100vh + 100px)) rotate(360deg); opacity: 0; }
}

.tetris-block {
  position: absolute;
  width: 30px;
  height: 30px;
  background: var(--primary);
  animation: tetrisDrop 15s linear infinite;
  opacity: 0;
  z-index: -1;
}

.tetris-block:nth-child(1) { left: 10%; animation-delay: 0s; }
.tetris-block:nth-child(2) { left: 20%; animation-delay: 3s; }
.tetris-block:nth-child(3) { left: 30%; animation-delay: 6s; }
.tetris-block:nth-child(4) { left: 40%; animation-delay: 9s; }
.tetris-block:nth-child(5) { left: 50%; animation-delay: 12s; }
.tetris-block:nth-child(6) { left: 60%; animation-delay: 15s; }
.tetris-block:nth-child(7) { left: 70%; animation-delay: 18s; }
.tetris-block:nth-child(8) { left: 80%; animation-delay: 21s; }
.tetris-block:nth-child(9) { left: 90%; animation-delay: 24s; }

.resume-header {
  text-align: center;
  margin-bottom: 4rem;
  padding: 2rem 0;
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  background: linear-gradient(145deg, rgba(10, 25, 47, 0.9), rgba(17, 34, 64, 0.8));
  box-shadow: 0 10px 30px -15px rgba(2, 12, 27, 0.7);
  border: 1px solid rgba(100, 255, 218, 0.1);
  transition: all 0.3s ease;
  font-size: 1.1rem;
}

.resume-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, transparent, var(--primary), transparent);
  animation: shine 3s infinite;
}

@keyframes shine {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

.resume-header h1 {
  font-size: 3.5rem;
  margin: 0 0 0.5rem;
  color: var(--primary);
  font-weight: 700;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-family: 'Space Grotesk', sans-serif;
  text-shadow: 0 0 10px rgba(100, 255, 218, 0.3);
  position: relative;
  display: inline-block;
}

.resume-header h1::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 50px;
  height: 3px;
  background: var(--primary);
  border-radius: 3px;
}

.resume-header .title {
  font-size: 1.6rem;
  color: var(--text);
  margin: 1rem 0 1.5rem;
  font-weight: 400;
  letter-spacing: 1px;
  font-family: 'Roboto Mono', monospace;
  text-transform: uppercase;
  opacity: 0.9;
}

.contact-info {
  display: flex;
  justify-content: center;
  gap: 2.5rem;
  flex-wrap: wrap;
  color: var(--secondary);
  font-size: 1.1rem;
  margin-top: 1.5rem;
}

.contact-info a {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--secondary);
  text-decoration: none;
  transition: all 0.3s ease;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  border: 1px solid transparent;
}

.contact-info a:hover {
  color: var(--primary);
  transform: translateY(-2px);
  background: rgba(100, 255, 218, 0.1);
  border-color: rgba(100, 255, 218, 0.2);
  box-shadow: 0 5px 15px rgba(100, 255, 218, 0.1);
}

.contact-info svg {
  font-size: 1.2em;
  color: var(--primary);
}

.resume-section {
  margin-bottom: 3rem;
  padding: 2rem;
  background: rgba(17, 34, 64, 0.6);
  border-radius: 12px;
  border: 1px solid rgba(100, 255, 218, 0.1);
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(5px);
}

.resume-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 3px;
  height: 100%;
  background: var(--primary);
  transform: scaleY(0);
  transform-origin: top;
  transition: transform 0.6s cubic-bezier(0.65, 0, 0.35, 1);
}

.resume-section:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 30px -10px rgba(2, 12, 27, 0.7);
  border-color: rgba(100, 255, 218, 0.2);
}

.resume-section:hover::before {
  transform: scaleY(1);
  transform-origin: bottom;
}

.resume-section h3 {
  color: var(--primary);
  margin: 0 0 1.8rem;
  font-size: 1.6rem;
  letter-spacing: 1px;
  position: relative;
  display: inline-flex;
  align-items: center;
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  padding-bottom: 10px;
}

.resume-section h3::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 3px;
  background: var(--primary);
  border-radius: 3px;
  transition: width 0.3s ease;
}

.resume-section:hover h3::after {
  width: 100%;
}

/* Tetris block decoration for section headers */
.resume-section h3 .bracket {
  display: inline-block;
  color: var(--primary);
  opacity: 0;
  transition: all 0.3s ease;
  line-height: 1;
  margin: 0 2px;
}

.resume-section h3 .bracket:first-child {
  transform: translateX(-10px);
  margin-right: 5px;
}

.resume-section h3 .bracket:last-child {
  transform: translateX(10px);
  margin-left: 5px;
}

.resume-section:hover h3 .bracket {
  opacity: 1;
  transform: translateX(0);
}

.resume-section ul {
  list-style: none;
  padding: 0;
}

.resume-section ul {
  padding-left: 1.2rem;
}

.resume-section li {
  margin-bottom: 0.8rem;
  padding-left: 1.8rem;
  position: relative;
  color: var(--text);
  line-height: 1.7;
  transition: all 0.2s ease;
}

.resume-section li:hover {
  color: white;
  transform: translateX(3px);
}

.resume-section li::before {
  content: 'â–¹';
  position: absolute;
  left: 0;
  color: var(--primary);
  font-size: 1.1em;
  line-height: 1.5;
  transition: transform 0.3s ease;
}

.resume-section li:hover::before {
  transform: rotate(90deg);
}

.job {
  margin-bottom: 2.5rem;
  padding: 1.8rem;
  background: rgba(10, 25, 47, 0.5);
  border-radius: 8px;
  border: 1px solid rgba(100, 255, 218, 0.08);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.job::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, transparent 65%, rgba(100, 255, 218, 0.03) 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.job:hover {
  transform: translateX(5px);
  border-color: rgba(100, 255, 218, 0.2);
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.2);
}

.job:hover::before {
  opacity: 1;
}

.job-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.job-header h4 {
  color: var(--primary);
  margin: 0;
  font-size: 1.3rem;
  font-weight: 600;
  letter-spacing: 0.5px;
  position: relative;
  display: inline-block;
}

.job-header h4::after {
  content: '';
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 0;
  height: 2px;
  background: var(--primary);
  transition: width 0.3s ease;
}

.job:hover .job-header h4::after {
  width: 100%;
}

.period {
  color: var(--secondary);
  font-size: 0.95rem;
  font-family: 'Roboto Mono', monospace;
  background: rgba(100, 255, 218, 0.1);
  padding: 0.3rem 0.8rem;
  border-radius: 15px;
  border: 1px solid rgba(100, 255, 218, 0.2);
}

.technologies {
  margin-top: 1.5rem;
  padding-top: 1.2rem;
  border-top: 1px solid rgba(100, 255, 218, 0.1);
  color: var(--secondary);
  font-size: 0.92rem;
  font-family: 'Roboto Mono', monospace;
  letter-spacing: 0.3px;
}

.technologies strong {
  color: var(--primary);
  font-weight: 500;
  margin-right: 0.5rem;
  position: relative;
  display: inline-block;
}

.technologies strong::after {
  content: ':';
  color: var(--primary);
  margin-left: 0.2rem;
}

.education-item {
  margin-bottom: 2rem;
  padding: 1.5rem;
  background: rgba(10, 25, 47, 0.5);
  border-radius: 8px;
  border-left: 3px solid var(--primary);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.education-item::before {
  content: 'ðŸŽ“';
  position: absolute;
  top: 1rem;
  right: 1.5rem;
  font-size: 1.8rem;
  opacity: 0.1;
  transition: all 0.3s ease;
}

.education-item:hover {
  transform: translateX(5px);
  background: rgba(10, 25, 47, 0.7);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.education-item:hover::before {
  transform: scale(1.2);
  opacity: 0.15;
}

.education-item h4 {
  color: var(--primary);
  margin: 0 0 0.5rem;
  font-size: 1.2rem;
  font-weight: 600;
  position: relative;
  display: inline-block;
}

.education-item h4::after {
  content: '';
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 0;
  height: 2px;
  background: var(--primary);
  transition: width 0.3s ease;
}

.education-item:hover h4::after {
  width: 100%;
}

.education-item p {
  color: var(--secondary);
  margin: 0.3rem 0 0;
  font-size: 1rem;
  font-family: 'Roboto Mono', monospace;
}

.tech-category {
  margin-bottom: 2rem;
  position: relative;
  padding: 1.5rem;
  background: rgba(10, 25, 47, 0.5);
  border-radius: 8px;
  border: 1px solid rgba(100, 255, 218, 0.1);
  transition: all 0.3s ease;
}

.tech-category:hover {
  transform: translateY(-3px);
  border-color: rgba(100, 255, 218, 0.2);
  box-shadow: 0 10px 20px -10px rgba(2, 12, 27, 0.5);
}

.tech-category h4 {
  color: var(--primary);
  margin: 0 0 1rem;
  font-size: 1.2rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.tech-category h4::before {
  content: 'â–¹';
  color: var(--primary);
  font-size: 1.2em;
  line-height: 1;
}

.tech-category p {
  color: var(--text);
  margin: 0;
  line-height: 1.8;
  font-size: 1.05rem;
  font-family: 'Roboto Mono', monospace;
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem 1.5rem;
}

.tech-category p span {
  position: relative;
  padding-left: 1.5rem;
}

.tech-category p span::before {
  content: 'â†’';
  position: absolute;
  left: 0;
  color: var(--primary);
  font-size: 0.9em;
  line-height: 1.5;
  opacity: 0.7;
  transition: all 0.2s ease;
}

.tech-category p span:hover {
  color: white;
}

.tech-category p span:hover::before {
  transform: translateX(3px);
  opacity: 1;
}

.languages-section li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 2rem;
}

.languages-section strong {
  color: #64ffda;
}

.download-section {
  text-align: center;
  margin-top: 3rem;
}

.download-button {
  display: inline-flex;
  align-items: center;
  gap: 0.8rem;
  padding: 1rem 2.2rem;
  background: transparent;
  color: var(--primary);
  border: 2px solid var(--primary);
  border-radius: 6px;
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 500;
  letter-spacing: 0.5px;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.download-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: rgba(100, 255, 218, 0.1);
  transition: width 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  z-index: -1;
}

.download-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px -10px rgba(100, 255, 218, 0.3);
  background: rgba(100, 255, 218, 0.05);
  color: white;
  border-color: var(--primary);
}

.download-button:hover::before {
  width: 100%;
}

.download-button svg {
  transition: transform 0.3s ease;
}

.download-button:hover svg {
  transform: translateY(-2px);
}

.event-link {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  color: #64ffda;
  text-decoration: none;
  font-size: 0.9rem;
  transition: all 0.3s ease;
  margin-top: 1rem;
}

.event-link:hover {
  color: #ffffff;
  transform: translateY(-2px);
}

.event-link svg {
  font-size: 1.1rem;
}

@media (max-width: 768px) {
  .resume-container {
    padding: 1rem;
  }

  .resume-header h1 {
    font-size: 2.5rem;
  }

  .resume-header .title {
    font-size: 1.2rem;
  }

  .contact-info {
    flex-direction: column;
    gap: 0.5rem;
    align-items: center;
  }

  .job-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .resume-section {
    padding: 1rem;
  }

  .download-button {
    padding: 0.8rem 1.6rem;
    font-size: 1rem;
  }
}

@media (max-width: 992px) {
  .resume-container {
    padding: 1.5rem 1rem;
  }
  
  .resume-header h1 {
    font-size: 2.8rem;
  }
  
  .resume-header .title {
    font-size: 1.4rem;
  }
  
  .resume-section {
    padding: 1.5rem;
  }
  
  .job, .education-item, .tech-category {
    padding: 1.5rem;
  }
}

@media (max-width: 768px) {
  .resume-header h1 {
    font-size: 2.2rem;
  }
  
  .resume-header .title {
    font-size: 1.2rem;
  }
  
  .contact-info {
    flex-direction: column;
    gap: 1rem;
    align-items: center;
  }
  
  .contact-info a {
    padding: 0.5rem 0.8rem;
    font-size: 1rem;
  }
  
  .job-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.8rem;
  }
  
  .period {
    margin-top: 0.5rem;
    display: inline-block;
  }
  
  .resume-section h3 {
    font-size: 1.4rem;
  }
  
  .job {
    padding: 1.5rem 1.2rem;
  }
  
  .tech-category p {
    font-size: 0.98rem;
  }
  
  .download-button {
    width: 100%;
    justify-content: center;
    padding: 1rem;
  }
}

@media (max-width: 480px) {
  .resume-header h1 {
    font-size: 1.8rem;
  }
  
  .resume-header .title {
    font-size: 1.1rem;
  }
  
  .resume-section {
    padding: 1.2rem;
  }
  
  .job, .education-item, .tech-category {
    padding: 1.2rem;
  }
  
  .resume-section h3 {
    font-size: 1.3rem;
  }
  
  .job-header h4 {
    font-size: 1.15rem;
  }
  
  .period {
    font-size: 0.85rem;
  }
  
  .resume-section li {
    font-size: 0.95rem;
    padding-left: 1.5rem;
  }
  
  .technologies {
    font-size: 0.88rem;
  }
  
  .tech-category p {
    font-size: 0.95rem;
    gap: 0.3rem 1rem;
  }
  
  .tech-category p span {
    padding-left: 1.2rem;
  }
}